.box-container{
    position: flex;
    justify-content: center;
    align-items: center;
    width: 400px;
    height: 400px;
    /* border: 1px solid black; */
    margin: 50px;
}

.box-container > *{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 400px;
    height: 400px;
    transition: transform 1s;
}

.front{
    position: relative;
}

.back{
    position: absolute;
    background: white;
}
.box-container:hover > .front{
    transform: translateX(-200px);
}
.box-container:hover > .back{
    transform: translateX(100px);
}

.back-content{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    width: 250px;
    height: 350px;
    margin-left: 90px;
    flex-direction: column;
    /* border: 1px solid black; */
}

@media (max-width: 500px){
    .box-container{
        margin-bottom: 0;
        width: 250px;
        height: 250px;

    }
    .box-container > *{
        width: 250px;
        height: 250px;
    }
    .back-content{
        width: 90%;
        margin-left: -20px;
    }
    .back-content > h3{
        font-size: 15px;
    }
    .back-content > h2{
        font-size: 20px;
    }
}